.LoadingSpinner_spinner__1T13Q {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.LoadingSpinner_spinner__1T13Q:after {
  content: ' ';
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid teal;
  border-color: teal transparent teal transparent;
  -webkit-animation: LoadingSpinner_spinner__1T13Q 1.2s linear infinite;
          animation: LoadingSpinner_spinner__1T13Q 1.2s linear infinite;
}
@-webkit-keyframes LoadingSpinner_spinner__1T13Q {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes LoadingSpinner_spinner__1T13Q {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

